import React from 'react';
import {Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle} from '@material-ui/core';

export const AlertDialog = ({alert, setAlert, message = '', callback = null}) => {

    const onAlertClose = () => {
        setAlert(false);
    };

    const onAlertAction = () => {
        const param = alert;
        setAlert(false);
        if (callback) {
            callback(param);
        }
    };

    return (
        <Dialog open={!!alert} onClose={onAlertClose}
                aria-labelledby='alert-dialog-title' aria-describedby='alert-dialog-description'>
            <DialogTitle id='alert-dialog-title'>{' !'}</DialogTitle>
            <DialogContent>
                <DialogContentText id='alert-dialog-description'>
                     ,   {message} ?
                </DialogContentText>
            </DialogContent>
            <DialogActions>
                <Button onClick={onAlertClose} color='primary' variant={'contained'} title='  '
                        autoFocus>
                      
                </Button>
                <Button onClick={onAlertAction} color='primary' title='!    !'>
                     
                </Button>
            </DialogActions>
        </Dialog>
    );
};